	export default {
		data() {
			return {
				authKey:''
			};
		},
		onLoad() {
			this.authKey = uni.getStorageSync('authKey');
		},
		methods:{
			join(){
				uni.request({
					url: this.http+'copemate/join',
					method: 'GET',
					header: {
						authKey: this.authKey
					},
					data: {},
					success: res => {
						if (res.data.code == 200) {
							uni.showToast({
								title: '成功加入合伙人',
								icon:'none',
								mask: false,
								duration: 1500
							});
// 							 uni.navigateTo({
// 								url: '/pages/mine/partnership/associate'
// 							});
						}else{
							uni.showToast({
								title: res.data.error,
								icon:'none',
								mask: false,
								duration: 1500
							});
						}
					},
				});
				
			}
		}
	}
